.burger.burger--open .burger__line {
  background-color: #011c40; }

.burger.burger--open .burger__line:nth-child(2) {
  transform: translateX(40px);
  transition: all 0.2s ease-out; }

.burger.burger--open .burger__line:nth-child(1) {
