.header {
  background-color: #00467e;
  background-image:
    url("../img/blocks/about/about.webp"),
    url("../img/blocks/about/about.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;

  @media (min-resolution: $retina-dppx) {
    background-image:
      url("../img/blocks/about/about@2x.webp"),
      url("../img/blocks/about/about@2x.jpeg");
  }
}

.header .container {
  @include vp-mobile {
    padding: 0;
  }
}

.header h2,
.header p {
  color: $color-white;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 35px 0;

  min-height: 38px;

  @include vp-tablet {
    padding: 10px 0;
  }

  @include vp-mobile {
    padding: 0;
  }

  &--open {
    @include vp-mobile {
      padding-left: 35px;
    }
  }
}

.header__logo {
  margin-right: auto;

  &--white {
    @include vp-mobile {
      display: none;
    }
  }

  &--blue {
    display: none;

    @include vp-mobile {
      display: flex;
    }
  }

  &--hidden {
    @include vp-mobile {
      display: none;
    }
  }

  &--visible {
    @include vp-mobile {
      position: relative;
      top: -6px;
      left: 15px;
      width: 50px;
      display: flex;
    }
  }
}

.header__nav {
  display: flex;
  align-items: center;
  margin-left: auto;
  min-height: 38px;
  max-width: 1000px;

  @include vp-mobile {
    position: relative;
    flex-direction: column;
    margin-left: 0;
    width: 100%;
    min-height: 75px;
    max-width: 600px;
    padding: 21px 17px 30px;
  }

  &--white-bg {
    @include vp-mobile {
      min-height: 100vh;
      overflow-y: auto;
      background-color: $color-white;
    }
  }
}

.header__navigation-list {
  padding-left: 10px;
  margin-left: 20px;

  @include vp-tablet {
    align-items: stretch;
    padding-top: 16px;
  }

  @include vp-mobile {
    &--hidden {
      display: none;
    }

    &--visible {
      position: absolute;
      min-height: 100vh;
      top: 26px;
      display: flex;
      padding: 14px 25px 80px;
      margin-left: 0;
      background-color: $color-white;
    }
  }
}

.header__navigation-list .navigation__item:not(:last-child) {
  @include vp-tablet {
    margin-bottom: 25px;
  }
}

.header__menu-bg {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: $color-tangaroa-70;

  &--open {
    display: block;
  }
}

.header__about {
  margin-top: 166px;
  padding-left: 95px;

  @include vp-tablet {
    margin-top: 185px;
    padding-left: 0;
  }

  @include vp-mobile {
    margin-top: 102px;
  }
}
